.button-ripple {
    position: relative;
    overflow: hidden;
    transition: background 400ms;
    background-color: white;
    padding: .5em;
    margin: .5em;
    font-size: 1.2em;
    font-weight: 600;
    outline: 0;
    color: #3D73E4;
    border: 2px solid #3D73E4;
    box-shadow: 3px 3px 3px rgba(0,0,0,0.3); /*black with 30% opacity*/
    cursor: pointer;
    -webkit-appearance: none;
    border-radius: 0; /* 0.25rem */
}

.button-ripple:hover {
    background-color: #F4F3F2;
}

.button-ripple:active {
    box-shadow: 2px 2px 2px rgba(0,0,0,0.3); /*black with 30% opacity*/
}

.button-ripple span.ripple {
    position: absolute;
    border-radius: 50%;
    transform: scale(0);
    animation: ripple 600ms linear;
    background-color: rgba(255,255,255,0.7); /*white with 70% opacity*/
}

@keyframes ripple {
    to {
        transform: scale(4);
        opacity: 0;
    }
}